<template>
  <div>
    <!-- Parte del encabezado de la página -->
    <!-- <div>
      <div>
        <b-jumbotron class="p-0">
          <div>
            <b-card
              class="border-0 rounded-0"
              overlay
              img-src="https://i.pinimg.com/originals/85/67/4c/85674cc2d3ff2a4666a866d1b4ca9de5.jpg"
              img-alt="Wind logo"
              text-variant="white"
            ></b-card>
          </div>
        </b-jumbotron>
      </div>
    </div> -->
    <!-- Parte del contenido -->
    <section v-if="isError">
      <p>Ha ocurrido un error</p>
    </section>
    <div class="container" style="padding-top: 5em" v-else>
      <h1>Requerimientos</h1>
      <section v-if="loading">Cargando....</section>
      <form id="formulario" v-else @submit.prevent="conectar">
        <!-- Identificador Nombre Tipo -->
        <div class="form-row">
          <!-- Identificador -->
          <div class="form-group col-md-3">
            <label for="id">Identificador</label>
            <input
              type="text"
              class="form-control"
              id="id"
              v-model="reg.id"
              placeholder="Jerarquizado: R1, R1.1, R1.1.1"
            />
          </div>
          <!-- Nombre -->
          <div class="form-group col-md-5">
            <label for="nombre">Nombre</label>
            <input
              type="text"
              class="form-control"
              id="nombre"
              v-model="reg.nombre"
              placeholder="Nombre (comenzar por un verbo)"
            />
          </div>
          <!-- Tipo -->
          <div class="form-group col-md-4">
            <label for="tipo">Tipo</label>
            <select id="tipo" class="form-control" v-model="reg.tipo">
              <option selected>Seleccionar...</option>
              <option>Necesario</option>
              <option>Deseable</option>
            </select>
          </div>
        </div>
        <!-- Requerimiento Critico Prioridad -->
        <div class="form-row">
          <!-- Requerimiento -->
          <div class="form-group col-md-3">
            <label for="req_que_utiliza">Requerimiento que lo utiliza</label>
            <input
              type="text"
              class="form-control"
              id="req_que_utiliza"
              v-model="reg.req_que_utiliza"
            />
          </div>
          <!-- Critico -->
          <div class="form-group col-md-5">
            <label for="critico">Crítico?</label>
            <input
              type="text"
              class="form-control"
              id="critico"
              v-model="reg.critico"
              placeholder="Requerimientos de eficiencia"
            />
          </div>
          <!-- Prioridad -->
          <div class="form-group col-md-4">
            <label for="prioridad">Prioridad</label>
            <select id="prioridad" class="form-control" v-model="reg.prioridad">
              <option selected>Seleccionar...</option>
              <option>0</option>
              <option>1</option>
              <option>2</option>
              <option>3</option>
              <option>5</option>
            </select>
          </div>
        </div>
        <!-- Documentos Entrada -->
        <div class="form-row">
          <!-- Documentos asociados -->
          <div class="form-group col-md-5">
            <label for="documentos">Documentos asociados</label>
            <input
              type="string"
              class="form-control-file"
              id="documentos"
              v-model="reg.documentos"
            />
          </div>
          <!-- Entrada -->
          <div class="form-group col-md-7">
            <label for="entrada">Entrada</label>
            <input
              type="text"
              class="form-control"
              id="entrada"
              v-model="reg.entrada"
            />
          </div>
        </div>
        <!-- Salida Descripcion -->
        <div class="form-row">
          <!-- Salida -->
          <div class="form-group col-md-6">
            <label for="salida">Salida</label>
            <input
              type="text"
              class="form-control"
              id="salida"
              v-model="reg.salida"
            />
          </div>
          <!-- Descripcion -->
          <div class="form-group col-md-6">
            <label for="descripcion">Descripción</label>
            <input
              type="text"
              class="form-control"
              id="descripcion"
              v-model="reg.descripcion"
              placeholder="Precondición, Descripción, Postcondición"
            />
          </div>
        </div>
        <!-- Manejo de situaciones -->
        <div class="form-group">
          <label for="situaciones">Manejo de situaciones anormales</label>
          <textarea
            class="form-control"
            id="situaciones"
            rows="4"
            v-model="reg.situaciones"
            placeholder="Describir cada situación anormal posible y el comportamiento asociado del sistema, con respecto a:
                  1.	Violaciones a la precondición
                  2.	Situaciones anormales con respecto a la utilización de recursos: espacio en disco, memoria, ...            "
          ></textarea>
          <!-- <p>{{ reg }}</p> -->
        </div>
        <!-- Final de formulario -->
        <div class="text-center">
          <button
            type="button"
            class="btn btn-warning"
            @click="$router.push('about')"
          >
            Cancelar
          </button>
          <button
            type="submit"
            class="btn btn-primary ml-2"
            @click="conectar()"
          >
            Finalizar
          </button>
        </div>
      </form>
    </div>
    <!-- PARTE DEL PIE DE PAGINA -->
    <div class="text-center sticky-bottom">
      <footer class="blockquote-footer">
        <p>
          &copy;Wind Derechos reservados | Fotos de
          <a href="https://co.pinterest.com/">Pinterest</a> | Diseñado con
          <a href="https://getbootstrap.com/" rel="nofollow">BOOTSTRAP</a> y
          <a href="https://vuejs.org/" rel="nofollow">VUE</a>
        </p>
      </footer>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapState, mapActions } from 'vuex';
const API = "http://localhost:3300/api";
export default {
  computed: {
    ...mapState(['reg'])
  },
  data() {
    return {
      reg: {
        id: "",
        nombre: "",
        tipo: "",
        req_que_utiliza: "",
        critico: "",
        prioridad: 0,
        documentos: "",
        entrada: "",
        salida: "",
        descripcion: "",
        situaciones: "",
      },
      loading: true,
      isError: false,
    };
  },
  methods: {
    ...mapActions(['setReg']),
    limpiar() {
      this.reg = {
        id: "",
        nombre: "",
        tipo: "",
        req_que_utiliza: "",
        critico: "",
        prioridad: 0,
        documentos: "",
        entrada: "",
        salida: "",
        descripcion: "",
        situaciones: "",
      };
    },
    conectar() {
      try {
        axios
          .post(`${API}/nuevo`, this.reg)
          .then((response) => {
            this.setReg(this.reg);
            this.limpiar();
            console.log(response);
            $router.push('about');
          })
          .catch((error) => {
            console.log("Ha ocurrido un error " + error);
          });
      } catch (error) {
        console.log(error);
      }
    },
  },
  mounted() {
    try {
      axios
        .get(`${API}/registro`)
        .then((response) => {
          this.limpiar();
          this.loading = false;
        })
        .catch((error) => {
          console.log("Ha ocurrido un error " + error);
          this.isError = true;
        });
    } catch (error) {
      console.log(error);
      this.isError = true;
    }
  },
  created() {},
};
</script>
<!-- Iniciar el scripts dando los valores necesarios, más info: https://dev.twitch.tv/docs/embed/everything -->